<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a class="icon icon_manue"> manue </a>
      </div>
      <h1>Sport Top For Women</h1>
      <div class="chartWrap">
        <span class="share iconfont"> &#xe650; </span>
        <span class="bag"> <em class="font10"> 9+ </em> </span>
        <a class="iconfont cart"> &#xe669; </a></div>
    </section>


    <van-button type="default" @click="confirm">confirm</van-button>
    <van-button type="default" @click="alert">alert</van-button>

    <van-button type="default" @click="show=true">图片预览</van-button>


    <van-image-preview
      v-model="show"
      :images="images"
      @change="onChange"
    >
      <template v-slot:index>第 {{index+1}} 页</template>
    </van-image-preview>

  </div>
</template>

<script>
export default {
  name: 'searchResult',
  data () {
    return {
      show: false,
      index: 0,
      images: [
        'static/images/consumerElectronicsPro1.png',
        'static/images/consumerElectronicsPro2.png'
      ]

    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    confirm(){
      this.$dialog.confirm({
        title: '标题',
        message: '弹窗内容',
        confirmButtonText:'confirm',
        cancelButtonText:'Cancel',
      }).then(() => {
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    alert(){
      this.$dialog.alert({
        message: '弹窗内容'
      });
    },
    onChange(index) {
      this.index = index;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/detail.scss";
</style>



